.SliderContainer{
    position: relative;

    min-width: 483px;
    max-width: 1200px;
    min-height: 600px;
    max-height: 800px;

    width: 90%;
    height: 600px;
    
    transform: translate(-50%, -50%);

    top: 50%;
    left: 50%;
}

.SliderCard{
    position: absolute;

    width: 100%;
    height: 100%;
    
    pointer-events: auto;

    background:coral;
}

.SliderCard a{
    z-index: inherit;
}

.SliderCard a.gameName{
    position: absolute;
    pointer-events: none;

    font-size: 30px;

    left: 10%;
    top: 15%;
}

.SliderCard a.context{
    position: absolute;
    pointer-events: none;

    font-size: 20px;

    left: 10%;
    top: 30%;
}

.SliderCard a.icon{
    position: absolute;

    width: 100px;
    height: 100px;

    left: 80%;
    top: 70%;
}

.SliderCard img{
    width: 100px;
    height: 100px;

    left: auto;
    top: auto;
}